import '../public/cart.css'
import '../public/cart_base.css'
import '../public/cart_trade.css'

import Vue from 'vue/dist/vue.js'
import url from './api'
import axios from 'axios'
import mixin from './mixin'
// import Cart from './cartService.js'
// import fetch from './fetch.js'


new Vue({
    el: '.container',
    data: {
        lists: null,
        total: 0,
        editingShop: null,
        editingShopIndex: -1,
        removePopup: false,
        removeData: null,
        removeMsg: '',
    },

    created(){
        this.getList()
    },

    computed: {
        allSelected:{
            get(){
                if (this.lists && this.lists.length) {
                    return this.lists.every(shop => {
                        return shop.checked
                    })
                }               
                return false
            },
            set(newVal){
                this.lists.forEach(shop => {
                    shop.checked = newVal
                    shop.goodsList.forEach(good => {
                        good.checked = newVal
                    })
                });
            }
        },
        allRemoveSelected:{
            get(){
                if (this.editingShop) {
                    return this.editingShop.removeChecked
                }
                return false
            },
            set(newVal){
                if (this.editingShop) {
                    this.editingShop.removeChecked = newVal
                    this.editingShop.goodsList.forEach(good => {
                        good.removeChecked = newVal
                    })
                }
            }
        },
        // 正常状态
        selectList(){
            if (this.lists && this.lists.length){
                let arr = []
                let total = 0
                this.lists.forEach(shop => {
                    shop.goodsList.forEach(good => {
                        if (good.checked) {
                            arr.push(good)
                            total += good.price * good.number
                        }
                    })
                })
                this.total = total
                return arr
            }
            return []
        },

        // 编辑状态 删除商品
        removeLists(){
            if (this.editingShop) {
                let arr = []
                this.editingShop.goodsList.forEach(good => {
                    if (good.removeChecked) {
                        arr.push(good)
                    }
                })
                return arr
            }
            return []
        }
    },

    methods: {
        getList(){
            axios.get(url.cartLists).then(res => {
                let lists = res.data.cartList
                lists.forEach(shop => {
                    shop.checked = true

                    // 编辑状态 删除商品
                    shop.removeChecked = false

                    shop.editing = false
                    shop.editingMsg = '编辑'
                    shop.goodsList.forEach(good => {
                        good.checked = true
                        good.removeChecked = false
                    });
                });
                this.lists = lists
              })
        },

        selectGood(shop,good){
            // 判断是否在编辑状态
            let attr = this.editingShop ? 'removeChecked' : 'checked'
            good[attr] = !good[attr]
            shop[attr] = shop.goodsList.every(good => {
                return good[attr]
            })
        },

        selectShop(shop){
            let attr = this.editingShop ? 'removeChecked' : 'checked'
            shop[attr] = !shop[attr]
            shop.goodsList.forEach(good => {
                good[attr] = shop[attr]
            });
        },

        selectAll(){
            let attr = this.editingShop ? 'allRemoveSelected' : 'allSelected'
            this[attr] = !this[attr]
        },

        edit(shop,shopIndex){
            // 被点编辑的商铺editing变为true
            shop.editing = !shop.editing
            // 被点编辑的商铺editingMsg变为完成
            shop.editingMsg = shop.editing ? '完成' : '编辑'
            // 遍历所有商铺
            this.lists.forEach((item,i) => {
                // 如果不是被点编辑的商铺 也就是其他商铺
                if (shopIndex !== i) {
                    // 其他商铺此时不可编辑
                    item.editing = false
                    // 被点编辑商铺editing为true 其他商铺editingMsg为空
                    item.editingMsg = shop.editing ? '' : '编辑'
                }
            })
            this.editingShop = shop.editing ? shop : null
            this.editingShopIndex = shop.editing ? shopIndex : -1
        },

        reduce(good){
            if(good.number == 1) return
            axios.post(url.cartReduce, {
                id: good.id,
                number: 1
            }).then(res => {
                good.number--
            })
            // Cart.reduce(good.id).then(res => {
            //     good.number--
            //   })
        },

        add(good){
            axios.post(url.addCart, {
                id: good.id,
                number: 1
             }).then(res => {
                 good.number++
             })
            // Cart.add(good.id).then(res => {
            //     good.number++
            //   })
        },

        remove(shop,shopIndex,good,goodIndex){
            this.removePopup = true
            this.removeData = {shop,shopIndex,good,goodIndex}
            this.removeMsg = '确定要删除该商品吗？'
            
        },

        removeConfirm(){         
            if(this.removeMsg == '确定要删除该商品吗？'){
                let {shop,shopIndex,good,goodIndex} = this.removeData
            axios.post(url.cartRemove,{
                id: good.id,
            }).then(res => {
                shop.goodsList.splice(goodIndex, 1)
                if(!shop.goodsList.length) {
                    // 移除店铺
                    this.lists.splice(shopIndex, 1)
                    this.removeShop()
                  }
                this.removePopup  = false
                })
            }else{
                let ids = []
                this.removeLists.forEach(good => {
                  ids.push(good.id)
                })
                axios.post(url.cartMrRemove, {
                  ids
                }).then(res => {
                  let arr = []
                  this.editingShop.goodsList.forEach(good => {
                    let index = this.removeLists.findIndex(item => {
                      return item.id == good.id
                    })
                    if(index === -1) {
                      arr.push(good)
                    }
                  })
                  if(arr.length) {
                    this.editingShop.goodsList = arr
                  } else {
                    this.lists.splice(this.editingShopIndex, 1)
                    this.removeShop()
                  }
                  this.removePopup  = false
                })
              }
            },

        removeShop(){
            this.editingShop = null
            this.editingShopIndex = -1
            this.lists.forEach(shop => {
            shop.editing = false
            shop.editingMsg = '编辑'
            })
        },

        removeList() {
            this.removePopup = true
            this.removeMsg = `确定将所选 ${this.removeLists.length} 个商品删除？`
        },

        
    },
    mixins: [mixin]
})